#flyoutMenu{
    width: 100vw;
    height: 100vh;
    background-color: #ffe600;
    position: fixed;
    top: 0;
    left: 0;
    transition: transform .5s cubic-bezier(0, .52, 0, 1);
    overflow: scroll;
    z-index: 1000;
}

#flyoutMenu.hide{
    transform: translate3d(-100vw, 0, 0);
}

#flyoutMenu.show{
    transform: translate3d(0vw, 0, 0);
    overflow: hidden;
}

#flyoutMenu h2 a {
    color: #333;
    margin-left: 15px;
    text-decoration: none;
}

#flyoutMenu h2 a:hover{
    text-decoration: underline;
}